/**
 * Middleware
 * Class for FetchHandler
 */
export class CustomFetchHandler {
  constructor(customFetch) {
    this.customFetch = customFetch;
  }
  /**
   * @inheritdoc
   */
  async execute(url, requestInit) {
    return await this.customFetch(url, requestInit);
  }
}
